import React from 'react';
import { Link } from "react-router-dom";
import { Card, Col, Row, PageHeader, Descriptions, Typography, Button  } from 'antd';

class InicioPag extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            publicaciones : []
        }  
        this.getPublicaciones();
    }

    getPublicaciones = async () => {
        const { data, error } = await this.props.supabase
            .from('publicaciones')
            .select()
            .order('fecha_publicacion', { ascending: false })
  
        if ( error == null) {
            this.setState({
                publicaciones : data
            }) 
        }
    }

    render() {
        const imgStyle = {
            height: "23em",
            width: "100%"
        };
        const cardStyle = {
            height: "100%"
        };        
        const { Title } = Typography;
        const meses = ["enero", "febrero", "marzo", "abril", "mayo", "junio", "julio", "agosto", "septiembre", "octubre", "noviembre", "diciembre"];

        return (
            <PageHeader ghost={false}>
                <Descriptions>
                    <Descriptions.Item span={3}><Title level={4}>ltimas publicaciones</Title></Descriptions.Item>
                    <Descriptions.Item span={3}>
                        <Row gutter={ [16, 16] } >
                            { this.state.publicaciones.map( publicacion => {
                                publicacion.linkTo = "/" + publicacion.categoria + "/" + publicacion.id;
                                let image = <img alt={"Imagen por defecto"} src="/imageMockup.png" style={imgStyle}/>
                                if ( publicacion.imagen != null ){
                                    image = <img alt={publicacion.descripcion_imagen} src={"/" + publicacion.imagen} style={imgStyle}/> 
                                }
                                const parts = publicacion.fecha_publicacion.split("-");
                                let stringFecha = parts[2] + " de " + meses[parts[1] - 1] + " de " + parts[0];
                                return ( 
                                    <Col xs={24} sm={24} md={24} lg={12} xl={12} >
                                        <Link to={ publicacion.linkTo }>
                                            <Card hoverable key={publicacion.id} title={publicacion.titulo} style={cardStyle} 
                                                cover={image}>
                                                <p><i>{stringFecha}</i></p>
                                                <p>{publicacion.descripcion_breve}</p>
                                                <Button type="secondary">Leer ms...</Button>
                                            </Card>
                                        </Link>
                                    </Col>  
                                )
                            })}
                        </Row>
                    </Descriptions.Item>
                </Descriptions>
            </PageHeader>
        )
    }
}

export default InicioPag;