import React from 'react';
import { Link } from "react-router-dom";
import { Card, Col, Row, PageHeader, Descriptions, Typography, Button } from 'antd';

class MusicaPag extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            publicaciones : []
        }  
        this.getPublicaciones();
    }

    getPublicaciones = async () => {
        const { data, error } = await this.props.supabase
            .from('publicaciones')
            .select()
            .order('fecha_publicacion')
            .eq('categoria', 'musica')
  
        if ( error == null) {
            this.setState({
                publicaciones : data
            }) 
        }
    }

    render() {
        const cardStyle = {
            height: "100%"
        };        
        const { Title } = Typography;
        const meses = ["enero", "febrero", "marzo", "abril", "mayo", "junio", "julio", "agosto", "septiembre", "octubre", "noviembre", "diciembre"];

        return (
            <div>
                <PageHeader ghost={false}>
                    <Descriptions>
                        <Descriptions.Item span={3}><Title level={4}>Msica</Title></Descriptions.Item>
                        <Descriptions.Item span={3}>Escucho infinitud de gneros, me encanta apreciar los diferentes estilos de msica, aunque tengo preferencias musicales como el pop rock y el indie espaol. Mi grupo favorito de msica es "El Canto del Loco", pero tambin me encantan "Miss Caffeina" y "Vetusta Morla". En esta seccin, hablar brevemente a cerca de estos tres grupos musicales.</Descriptions.Item>
                        <Descriptions.Item span={3}>
                            <Row gutter={ [30, 30] }>
                                { this.state.publicaciones.map( publicacion => {
                                    publicacion.linkTo = "/musica/" + publicacion.id;
                                    let image = <img src="/imageMockup.png"/>
                                    if ( publicacion.imagen != null ){
                                        image = <img src={"/" + publicacion.imagen}/> 
                                    }
                                    const parts = publicacion.fecha_publicacion.split("-");
                                    let stringFecha = parts[2] + " de " + meses[parts[1] - 1] + " de " + parts[0];
                                    return ( 
                                        <Col xs={24} sm={24} md={24} lg={24} xl={24} >
                                            <Link to={ publicacion.linkTo }>
                                                <Card hoverable key={publicacion.id} title={ publicacion.titulo } style={ cardStyle } 
                                                    cover={ image }>
                                                    <p><i>{stringFecha}</i></p>
                                                    <p>{publicacion.descripcion_breve}</p>
                                                    <Button type="secondary">Leer ms...</Button>
                                                </Card>
                                            </Link>
                                        </Col>  
                                    )
                                })}
                            </Row>
                        </Descriptions.Item>
                    </Descriptions>
                </PageHeader>
            </div>
        )
    }
}

export default MusicaPag;