import React from 'react';
import { PageHeader, Image, Descriptions, Typography } from 'antd';

class SobreMiPag extends React.Component {

    render() {
        const imgStyle = {
            width: "30rem",
            height: "100%"
        };
        const { Title } = Typography;
        
        return ( 
            <PageHeader ghost={false}>
                    <Descriptions>
                        <Descriptions.Item span={3}><Title level={4}>Ins Daz del Rey</Title></Descriptions.Item>
                        <Descriptions.Item span={3}>
                            <Image src={"/me.png"} alt={"Imagen de Ins Daz del Rey."} style={imgStyle}/>
                        </Descriptions.Item>
                        <Descriptions.Item span={3}>
                        Soy una chica de 22 aos de la localidad de Pozoblanco, Crdoba. Curs mi grado de Ingeniera de la Salud 
                        con especialidad en Bioinformtica en la Universidad de Mlaga. Actualmente, estoy cursando un Mster en 
                        Ingeniera Web en la Universidad de Oviedo y tengo ambiciones de trabajar en este mbito. Adoro vivir 
                        nuevas experiencias y estar en un continuo aprendizaje. Me considero una persona trabajadora y responsable 
                        que se implica en lo que se propone.     
                        </Descriptions.Item>
                    </Descriptions>
                </PageHeader>
        )
    }
}

export default SobreMiPag;